body {
  font-family: 'Open Sans', sans-serif; }

.cs-hide {
  display: none !important; }

.setup {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .setup .mdc-card {
    width: 65%;
    max-width: 900px;
    height: auto;
    min-height: 500px; }
    @media only screen and (max-width: 992px) {
      .setup .mdc-card {
        width: 90%; } }

.setup-card {
  display: flex; }
  .setup-card__form {
    width: 60%;
    height: auto;
    min-height: 500px;
    padding: 32px; }
    @media only screen and (max-width: 768px) {
      .setup-card__form {
        width: 100%; } }
    .setup-card__form form {
      margin-top: 16px;
      display: flex;
      flex-direction: column; }
    .setup-card__form .text-field-container {
      width: 100%;
      margin-top: 32px; }
    .setup-card__form .mdc-text-field-helper-line {
      padding: 0px; }
    .setup-card__form .mdc-text-field {
      width: 100%;
      height: 40px; }
      .setup-card__form .mdc-text-field .mdc-text-field__input {
        font-size: 14px;
        padding: 7px 11px; }
      .setup-card__form .mdc-text-field .mdc-floating-label {
        font-size: 14px;
        top: 11px; }
      .setup-card__form .mdc-text-field .mdc-floating-label--float-above {
        transform: translateY(-96%) scale(0.75) !important; }
      .setup-card__form .mdc-text-field .id-status {
        position: absolute;
        right: 0;
        margin: 11px 12px;
        font-size: 12px;
        color: #333648;
        font-weight: 600; }
      .setup-card__form .mdc-text-field #cruxpayId {
        padding-right: 100px; }
    .setup-card__form .validation-rules {
      margin-top: 12px;
      opacity: 0; }
      .setup-card__form .validation-rules .rule {
        display: flex;
        align-items: center;
        margin-top: 8px; }
        .setup-card__form .validation-rules .rule .material-icons {
          font-size: 16px;
          margin-right: 12px; }
        .setup-card__form .validation-rules .rule span {
          font-weight: 400;
          font-size: 12px; }
      .setup-card__form .validation-rules .rule-isValid .icon-invalid {
        display: none; }
      .setup-card__form .validation-rules .rule-isValid .icon-valid {
        display: block; }
      .setup-card__form .validation-rules .rule-isValid .material-icons {
        color: #2ed573; }
      .setup-card__form .validation-rules .rule-isValid span {
        color: #2ed573; }
      .setup-card__form .validation-rules .rule-isInvalid .icon-invalid {
        display: block; }
      .setup-card__form .validation-rules .rule-isInvalid .icon-valid {
        display: none; }
      .setup-card__form .validation-rules .rule-isInvalid .material-icons {
        color: #eb4d4b; }
      .setup-card__form .validation-rules .rule-isInvalid span {
        color: #eb4d4b; }
    .setup-card__form .helper-text--success {
      color: #736df8 !important; }
  .setup-card__description {
    width: 40%;
    height: auto;
    min-height: 500px;
    padding: 32px;
    display: flex;
    flex-direction: column; }
    .setup-card__description img {
      display: block;
      margin: auto; }
    .setup-card__description__footer {
      display: flex;
      align-items: center;
      margin-top: auto; }
      .setup-card__description__footer .mdc-button {
        margin-left: auto; }
    @media only screen and (max-width: 768px) {
      .setup-card__description {
        display: none; } }
  .setup-card__brand {
    color: #333648;
    font-weight: 700;
    font-size: 24px; }
  .setup-card__headline {
    color: #333648;
    font-weight: 400;
    font-size: 24px;
    padding-top: 16px;
    text-align: left; }
  .setup-card__body {
    color: #333648;
    font-weight: 400;
    font-size: 16px;
    margin-top: 8px; }

.customisation {
  display: flex; }
  .customisation__selection {
    width: 50%;
    height: auto;
    min-height: 500px;
    padding: 32px;
    border-right: 1px solid #e3e8ef; }
  .customisation__description {
    width: 50%;
    height: auto;
    min-height: 500px;
    padding: 32px; }
  .customisation__brand {
    color: #333648;
    font-weight: 700;
    font-size: 24px; }
  .customisation__headline {
    color: #333648;
    font-weight: 400;
    font-size: 18px;
    margin-top: 8px; }
  .customisation__search {
    margin: 24px 0px; }
    .customisation__search .mdc-text-field {
      width: 100%;
      height: 40px; }
      .customisation__search .mdc-text-field .mdc-text-field__input {
        font-size: 14px;
        padding: 7px 11px; }
      .customisation__search .mdc-text-field .mdc-floating-label {
        font-size: 14px;
        top: 11px; }
      .customisation__search .mdc-text-field .mdc-floating-label--float-above {
        transform: translateY(-96%) scale(0.75) !important; }
      .customisation__search .mdc-text-field .mdc-text-field__icon {
        top: 0;
        bottom: 0;
        margin: 12px 5px;
        font-size: 18px;
        color: #8a8a8a; }
  .customisation__currency {
    width: 100%;
    min-height: 45px;
    display: flex;
    align-items: center;
    border: 1px solid #dadada;
    border-radius: 4px;
    margin: 16px 0px;
    cursor: pointer; }
  .customisation__currency-logo {
    width: 24px;
    height: 24px;
    margin-left: 16px; }
  .customisation__currency-name {
    color: #333648;
    font-size: 16px;
    margin-left: 16px;
    margin-right: 16px; }
  .customisation__currency-symbol {
    color: #333648;
    font-size: 16px;
    font-weight: 700;
    margin-left: auto;
    margin-right: 16px; }
